.App .App-Header {
  display: block;
  text-align: center;
}

.App .recharts-wrapper {
  margin: auto;
}

.about {
  padding-left: 10%;
}

.spacer {
  height: 80px;
}

@media (max-width: 300px) {
  .spacer {
    height: 5px;
  }
}

@media (min-width: 300px) and (max-width: 400px) {
  .spacer {
    height: 10px;
  }
}

@media (min-width: 400px) and (max-width: 479px) {
  .spacer {
    height: 20px;
  }
}

@media (min-width: 479px) and (max-width: 620px) {
  .spacer {
    height: 30px;
  }
}

@media (min-width: 620px) and (max-width: 767px) {
  .spacer {
    height: 40px;
  }
}

@media (min-width: 767px) {
  .spacer {
    height: 80px;
  }
}

